"use client";
import React from "react";
import { Project } from "@/data/projects";
import { useTranslation } from "@/hooks/useTranslation";
import { Button } from "@/components/ui/button";
import { Card, CardHeader, CardTitle, CardContent, CardFooter } from "@/components/ui/card";

interface ProjectsModalProps {
  isOpen: boolean;
  onClose: () => void;
  projects: Project[];
  technology: string;
}

const ProjectsModal: React.FC<ProjectsModalProps> = ({ isOpen, onClose, projects, technology }) => {
  const { t } = useTranslation();

  if (!isOpen) return null;

  const getDifficultyColor = (difficulty: string) => {
    switch (difficulty) {
      case "Beginner":
        return "bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200";
      case "Intermediate":
        return "bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-200";
      case "Advanced":
        return "bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200";
      default:
        return "bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-200";
    }
  };

  const getCategoryColor = (category: string) => {
    switch (category) {
      case "Frontend":
        return "bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-200";
      case "Backend":
        return "bg-purple-100 text-purple-800 dark:bg-purple-900 dark:text-purple-200";
      case "Full Stack":
        return "bg-indigo-100 text-indigo-800 dark:bg-indigo-900 dark:text-indigo-200";
      case "Mobile":
        return "bg-pink-100 text-pink-800 dark:bg-pink-900 dark:text-pink-200";
      case "Desktop":
        return "bg-orange-100 text-orange-800 dark:bg-orange-900 dark:text-orange-200";
      default:
        return "bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-200";
    }
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
      <div className="bg-white dark:bg-gray-800 rounded-lg max-w-6xl w-full max-h-[90vh] overflow-y-auto">
        <div className="sticky top-0 bg-white dark:bg-gray-800 border-b border-gray-200 dark:border-gray-700 p-6">
          <div className="flex justify-between items-center">
            <h2 className="text-2xl font-bold text-gray-900 dark:text-white">
              {technology} {t("viewProjects")}
            </h2>
            <Button
              variant="outline"
              onClick={onClose}
              className="text-gray-600 dark:text-gray-300"
            >
              {t("closeProjects")}
            </Button>
          </div>
        </div>

        <div className="p-6">
          <div className="grid gap-6 grid-cols-1 lg:grid-cols-2">
            {projects.map((project) => (
              <Card key={project.id} className="border-2 hover:shadow-lg transition-shadow dark:bg-gray-900 dark:border-gray-700">
                <CardHeader>
                  <div className="flex justify-between items-start mb-2">
                    <CardTitle className="text-xl font-bold text-gray-900 dark:text-white">
                      {project.title}
                    </CardTitle>
                    <div className="flex gap-2">
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${getDifficultyColor(project.difficulty)}`}>
                        {project.difficulty}
                      </span>
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${getCategoryColor(project.category)}`}>
                        {project.category}
                      </span>
                    </div>
                  </div>
                </CardHeader>
                
                <CardContent className="space-y-4">
                  <p className="text-gray-700 dark:text-gray-300 leading-relaxed">
                    {project.description}
                  </p>
                  
                  <div>
                    <h4 className="font-semibold text-gray-900 dark:text-white mb-2">Technologies:</h4>
                    <div className="flex flex-wrap gap-2">
                      {project.technologies.map((tech, index) => (
                        <span
                          key={index}
                          className="px-2 py-1 bg-gray-100 dark:bg-gray-700 text-gray-700 dark:text-gray-300 rounded text-sm"
                        >
                          {tech}
                        </span>
                      ))}
                    </div>
                  </div>

                  <div>
                    <h4 className="font-semibold text-gray-900 dark:text-white mb-2">Key Features:</h4>
                    <ul className="space-y-1">
                      {project.features.map((feature, index) => (
                        <li key={index} className="text-sm text-gray-600 dark:text-gray-400 flex items-center">
                          <span className="w-2 h-2 bg-gray-400 dark:bg-gray-500 rounded-full mr-2"></span>
                          {feature}
                        </li>
                      ))}
                    </ul>
                  </div>
                </CardContent>

                <CardFooter className="flex gap-3">
                  {project.githubUrl && (
                    <Button
                      variant="outline"
                      className="flex-1"
                      onClick={() => {
                        if (project.githubUrl && project.githubUrl.startsWith('/projects/')) {
                          // Open local project files in new tab
                          window.open(project.githubUrl, '_blank');
                        } else if (project.githubUrl) {
                          // Open external GitHub links
                          window.open(project.githubUrl, '_blank');
                        }
                      }}
                    >
                      {project.githubUrl && project.githubUrl.startsWith('/projects/') ? 'Ver Código' : t("viewCode")}
                    </Button>
                  )}
                  {project.liveUrl && (
                    <Button
                      className="flex-1 bg-black text-white hover:bg-gray-800 dark:bg-white dark:text-black dark:hover:bg-gray-200"
                      onClick={() => {
                        if (project.liveUrl && project.liveUrl.startsWith('/projects/')) {
                          // Open local project files in new tab
                          window.open(project.liveUrl, '_blank');
                        } else if (project.liveUrl) {
                          // Open external demo links
                          window.open(project.liveUrl, '_blank');
                        }
                      }}
                    >
                      {project.liveUrl && project.liveUrl.startsWith('/projects/') ? 'Executar' : t("liveDemo")}
                    </Button>
                  )}
                  {!project.githubUrl && !project.liveUrl && (
                    <Button
                      variant="outline"
                      className="flex-1"
                      onClick={() => alert(`${project.title} - Coming soon!`)}
                    >
                      Coming Soon
                    </Button>
                  )}
                </CardFooter>
              </Card>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProjectsModal;
